import { useState } from 'react';
import { ethers } from "ethers";
import { Navigate  } from "react-router-dom";
import { Row, Form, Button ,Spinner} from 'react-bootstrap';
import { create as ipfsHttpClient } from 'ipfs-http-client';
const client = ipfsHttpClient('https://ipfs.infura.io:5001/api/v0');

const Create = ({ marketplace, nft }) => {
  const [image, setImage] = useState('');
  const [price, setPrice] = useState(null);
  const [name, setName] = useState('');
  const [redirect,setredirect] = useState(false);
  const [isLoading, setLoading] = useState(false);
  const [description, setDescription] = useState('');
  const uploadToIPFS = async (event) => {
    event.preventDefault();
    const file = event.target.files[0];
    if (typeof file !== 'undefined') {
      try {
        const result = await client.add(file);
        console.log(result);
        setImage(`https://ipfs.infura.io/ipfs/${result.path}`);
      } catch (error){
        console.log("ipfs image upload error: ", error);
      }
    }
  }
  const createNFT = async () => {
    setLoading(true);
    if (!image || !price || !name || !description){
      setLoading(false);
      return;
    } 
    try{
      const result = await client.add(JSON.stringify({image, price, name, description}));
      console.log(result);
      mintThenList(result);

    } catch(error) {
      console.log("ipfs uri upload error: ", error);
    }
  }
  const mintThenList = async (result) => {
    const uri = `https://ipfs.infura.io/ipfs/${result.path}`;

    const listingPrice = ethers.utils.parseEther(price.toString());
    try{
      await(await marketplace.createNFT(uri, listingPrice)).wait();
      setLoading(false);
      setredirect(true);
      
    } catch(error) {
      console.log("ipfs uri upload error: ", error);
      setLoading(false);
    }
    
    
  }
  if(redirect) {
    return<Navigate  to="/ethereum-boilerplate/"/>
  }
  return (
    <div className="container-fluid mt-5">
      <div className="row">
        <main role="main" className="col-lg-12 mx-auto" style={{ maxWidth: '1000px' }}>
          <div className="content mx-auto">
            <Row className="g-4">
              <Form.Control
                type="file"
                required
                name="file"
                onChange={uploadToIPFS}
              />
              <Form.Control onChange={(e) => setName(e.target.value)} size="lg" required type="text" placeholder="Name" />
              <Form.Control onChange={(e) => setDescription(e.target.value)} size="lg" required as="textarea" placeholder="Description" />
              <Form.Control onChange={(e) => setPrice(e.target.value)} size="lg" required type="number" placeholder="Price in ETH" />
              <div className="d-grid px-0">
                <Button onClick={createNFT}  variant="primary" size="lg">{ isLoading ? <Spinner
                    as="span"
                    animation="border"
                    size="sm"
                    role="status"
                    aria-hidden="true"
                   
                    /> : 
                    
                  'Create and list NFT'}
                </Button>
              </div>
            </Row>
          </div>
        </main>
      </div>
    </div>
  );
}

export default Create;